body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.switch {
        font-size: 17px;
        position: relative;
        display: inline-block;
        width: 70px;
        height: 34.5px;
        overflow: hidden;
      }
      
      .switch input {
        opacity: 0;
        width: 0;
        height: 0;
      }
      
      .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        transition: .4s;
        border-radius: 30px;
      }
      
      .commutator {
        cursor: pointer;
        position: absolute;
        top: -29%;
        left: -14%;
        transition: .4s;
      }
      
      .iron-man {
        position: absolute;
        top: -27%;
        left: -100%;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
      }
      
      .input:checked + .slider {
        background-color: #2196F3;
      }
      
      .input:checked + .slider .commutator {
        -webkit-transform: translateX(34.5px);
                transform: translateX(34.5px);
        opacity: 0;
      }
      
      .input:checked + .slider .iron-man {
        -webkit-animation: show_5911 1.3s ease-in-out forwards;
                animation: show_5911 1.3s ease-in-out forwards;
      }
      
      @-webkit-keyframes show_5911 {
        0% {
          -webkit-transform: translateX(0px) rotate(90deg);
                  transform: translateX(0px) rotate(90deg);
        }
      
        50% {
          -webkit-transform: translateX(75px) rotate(90deg);
                  transform: translateX(75px) rotate(90deg);
        }
      
        100% {
          -webkit-transform: translateX(79px) rotate(0deg);
                  transform: translateX(79px) rotate(0deg);
        }
      }
      
      @keyframes show_5911 {
        0% {
          -webkit-transform: translateX(0px) rotate(90deg);
                  transform: translateX(0px) rotate(90deg);
        }
      
        50% {
          -webkit-transform: translateX(75px) rotate(90deg);
                  transform: translateX(75px) rotate(90deg);
        }
      
        100% {
          -webkit-transform: translateX(79px) rotate(0deg);
                  transform: translateX(79px) rotate(0deg);
        }
      }
